
// Libraries
import React from 'react';

// Components
import DividedContent from '../../components/Landing/DividedContent';
import TapShops from '../../components/Landing/TapShops';
import Header from '../../components/Landing/Header';
import Footer from '../../components/Landing/Footer';

// Assets
import slide2_1 from '../../assets/landing/2-1.svg';
import slide2_2 from '../../assets/landing/2-2.svg';
import slide3_1 from '../../assets/landing/3-1.svg';
import slide3_2 from '../../assets/landing/3-2.svg';
import slide4_1 from '../../assets/landing/4-1.svg';
import slide4_2 from '../../assets/landing/4-2.svg';
import slide5_1 from '../../assets/landing/5-1.svg';
import slide5_2 from '../../assets/landing/5-2.svg';


const Landing = () => {
  const Contents = [
    {
      prevBackgroundColor: '#452DA5',
      backgroundColor: '#fff',
      direction: 'row-reverse',
      title1: '',
      title2: '      ',
      texts: [
        'Instagram         ,    .',
        '  TapShop            ,   ,      ,      .'
      ],
      linkUrl: '/constructor',
      linkText: ' ',
      imageSmall: slide2_1,
      imagesCarousel: [
        slide2_2
      ],
      imageSignatureText: '',
      imageSignatureBackgroundColor: '#E84B47',
      morePaddingTop: true
    },
    {
      prevBackgroundColor: '#fff',
      backgroundColor: '#F8F7FF',
      title1: '',
      title2: '  ',
      texts: [
        '     ,  ,      .',
        '  ,     ,   .        ,         .'
      ],
      linkUrl: '/constructor',
      linkText: ' ',
      imageSmall: slide3_1,
      imagesCarousel: [
        slide3_2
      ],
      imageSignatureText: '',
      imageSignatureBackgroundColor: '#E84B47'
    },
    {
      prevBackgroundColor: '#F8F7FF',
      backgroundColor: '#fff',
      direction: 'row-reverse',
      title1: '-',
      title2: '    ',
      texts: [
        '         Instagram.  TapShop    -  10     .'
      ],
      linkUrl: '/constructor',
      linkText: ' ',
      imageSmall: slide4_2,
      imagesCarousel: [
        slide4_1
      ],
      imageSignatureText: ' 10 ',
      imageSignatureBackgroundColor: '#70BCF8'
    },
    {
      prevBackgroundColor: '#fff',
      backgroundColor: '#F8F7FF',
      title1: '',
      title2: '       ',
      texts: [
        '   ,     .',
        '    ,        ,         .'
      ],
      linkUrl: '/constructor',
      linkText: ' ',
      imageSmall: slide5_1,
      imagesCarousel: [
        slide5_2
      ],
      imageSignatureText: '',
      imageSignatureBackgroundColor: '#E84B47'
    },
  ]

  return (
    <div>
      <Header />
      {Contents.map((content, i) => <DividedContent key={i} {...content} />)}
      <TapShops waveColor={Contents[Contents.length - 1].backgroundColor} />
      <Footer />
    </div>
  )
}

export default Landing;